<template>
  <v-sheet class="pa-4 rounded-t backdrop">
    <p ref="title" class="text-h4 text-center topText mb-1 mt-4">
       
    </p>
    <v-stepper v-model="currentStep" class="transparentArea" alt-labels>
      <v-stepper-header class="elevation-0">
        <v-stepper-step :complete="currentStep > 1" step="1">
           
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="currentStep > 2" step="2">
           
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step step="3">  </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content class="pt-0 px-0" step="1">
          <v-card class="area mb-4 px-4 d-flex align-center" height="300px">
            <v-container>
              <v-row>
                <v-col>
                  <p class="text-center text-subtitle-1">
                     ?
                  </p>
                  <p>        .</p>
                  <p>
                         . 
                         .
                  </p>
                </v-col>
              </v-row>
            </v-container>
          </v-card>
          <div class="actionsWrapper">
            <v-btn
              class="stuff nextButton"
              ref="step2Button"
              @click="gotoStep2()"
            >
              
            </v-btn>
          </div>
        </v-stepper-content>

        <v-stepper-content class="pt-0 px-0" step="2">
          <v-card class="area mb-4 px-4 d-flex align-center" height="300px">
            <v-container>
              <v-row>
                <v-col>
                  <p class="text-center text-subtitle-1">
                          .
                  </p>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="withdrawForm.email"
                    label=""
                    class="textField"
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col>
                  <v-text-field
                    v-model="withdrawForm.pw"
                    label=""
                    type="password"
                    class="textField"
                    required
                    @keyup.enter="gotoStep3()"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
          </v-card>
          <div class="actionsWrapper">
            <v-btn
              class="stuff nextButton"
              ref="step3Button"
              @click="gotoStep3()"
            >
              
            </v-btn>
          </div>
        </v-stepper-content>

        <v-stepper-content class="pt-0 px-0" step="3">
          <v-card class="area mb-4 px-4 d-flex align-center" height="300px">
            <v-container>
              <v-row>
                <v-col>
                  <p class="text-center text-subtitle-1"> !</p>
                  <p class="text-center">  .</p>
                </v-col>
              </v-row>
            </v-container>
          </v-card>
          <div class="actionsWrapper">
            <v-btn class="stuff nextButton" @click="finish()">  </v-btn>
          </div>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </v-sheet>
</template>

<script>
/* global axios */

export default {
  data() {
    return {
      currentStep: 1,
      withdrawForm: {
        email: "",
        pw: "",
      },
    };
  },
  computed: {},
  methods: {
    async gotoStep2() {
      this.currentStep = 2;
    },
    async gotoStep3() {
      let response = await axios.post("/ajax/withdraw", this.withdrawForm);
      if (response.data.isOk == true) {
        this.currentStep = 3;
        this.$alertElastic(this.$refs.title, response.data.msg);
      } else {
        this.$alertElastic(this.$refs.step3Button.$el, response.data.msg);
      }
    },
    async finish() {
      // 
      this.$parent.$parent.$emit("input", false);
      //  
      this.$root.getUserInfo();
      //
      this.currentStep = 1;
      this.withdrawForm.email = "";
      this.withdrawForm.pw = "";
    },
  },
  watch: {},
  created() {},
  mounted() {},
  destroyed() {},
};
</script>

<style lang="scss" scoped>
.transparentArea {
  background-color: rgba(0, 0, 0, 0);
  box-shadow: unset;
}
.nextButton {
  width: 120px;
}
.actionsWrapper {
  text-align: center;
}
.textField {
  max-width: 400px;
  margin: auto;
}
</style>